html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

ul {
  list-style: none;
}

button,
input,
select {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

:root {
  --font-family: "Inter", sans-serif;
  --font-thin: 300;
  --font-normal: 400;
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-bold: 700;
  --font-x-bold: 800;
}

:root {
  --lime-green: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-linear-from: hsl(37, 97%, 70%);
  --instagram-linear-to: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --light-theme-toggle-btn: hsl(230, 22%, 74%);
  --light-theme-background: hsl(0, 0%, 100%);
  --light-theme-top-bg-pattern: hsl(225, 100%, 98%);
  --light-theme-card-bg: hsl(227, 47%, 96%);
  --light-theme-text-gray-blue: hsl(228, 12%, 44%);
  --light-theme-text-dark: hsl(230, 17%, 14%);
  --dark-theme-toggle-btn-linear-from: hsl(210, 78%, 56%);
  --dark-theme-toggle-btn-linear-to: hsl(146, 68%, 55%);
  --dark-theme-background-dark-blue: hsl(230, 17%, 14%);
  --dark-theme-top-background-pattern: hsl(232, 19%, 15%);
  --dark-theme-card-background: hsl(228, 28%, 20%);
  --dark-theme-text-desaturated-blue: hsl(228, 34%, 66%);
  --dark-theme-text-white: hsl(0, 0%, 100%);
}

html {
  font-size: 55%;
  scroll-behavior: smooth;
  scroll-padding-top: 6rem;
}
html::-webkit-scrollbar {
  width: 0.5rem;
}
html::-webkit-scrollbar-track {
  background: transparent;
}
html::-webkit-scrollbar-thumb {
  background-image: linear-gradient(to bottom, var(--dark-theme-toggle-btn-linear-from), var(--dark-theme-toggle-btn-linear-to));
  border-radius: 15px;
}
html::-moz-scrollbar {
  width: 0.5rem;
}
html::-moz-scrollbar-track {
  background: transparent;
}
html::-moz-scrollbar-thumb {
  background-image: linear-gradient(to bottom, var(--dark-theme-toggle-btn-linear-from), var(--dark-theme-toggle-btn-linear-to));
  border-radius: 15px;
}

scrollbar {
  width: 0.5rem;
}

scrollbar-track {
  background: transparent;
}

scrollbar-thumb {
  background-image: linear-gradient(to bottom, var(--dark-theme-toggle-btn-linear-from), var(--dark-theme-toggle-btn-linear-to));
  border-radius: 15px;
}

body {
  font-family: var(--font-family);
  font-size: 1.6rem;
  background-color: var(--dark-theme-background-dark-blue);
  padding: 2rem;
}

.dashboard__header {
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
}

.dashboard__header-text {
  padding: 0 0 1.9rem 0;
  border-bottom: 1px solid var(--dark-theme-text-desaturated-blue);
}
.dashboard__header-text h1 {
  font-size: 2.3rem;
  font-weight: var(--font-bold);
  color: var(--dark-theme-text-white);
  line-height: 1.6;
}
.dashboard__header-text p {
  font-size: 1.4rem;
  font-weight: var(--font-bold);
  color: var(--dark-theme-text-desaturated-blue);
}

.dashboard__toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1.9rem;
}
.dashboard__toggle .dashboard__toggle-title h3 {
  font-size: 1.6rem;
  font-weight: var(--font-bold);
  color: var(--dark-theme-text-desaturated-blue);
}

.dashboard__toggle-btn {
  width: 5rem;
  height: 2.5rem;
  border-radius: 5rem;
  background-image: linear-gradient(to right, var(--dark-theme-toggle-btn-linear-from), var(--dark-theme-toggle-btn-linear-to));
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.dashboard__toggle-btn .toggle-indicator {
  width: 1.8rem;
  height: 1.8rem;
  border-radius: 50%;
  background-color: var(--dark-theme-top-background-pattern);
  position: absolute;
  top: 3px;
  left: 3px;
  transition: left 500ms ease;
}

.social {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 1fr);
  place-items: center;
  gap: 2rem;
  margin-bottom: 3rem;
}

.social__dashboard {
  width: 100%;
  max-width: 50rem;
  height: 23rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--dark-theme-card-background);
  border-top-left-radius: 0.6rem;
  border-top-right-radius: 0.6rem;
  border-bottom-left-radius: 0.8rem;
  border-bottom-right-radius: 0.8rem;
  border-top: 4px solid;
  border-top-color: var(--facebook);
}

.social__dashboard-title {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}
.social__dashboard-title div {
  width: 3rem;
  height: 3rem;
}
.social__dashboard-title div img {
  width: 100%;
}
.social__dashboard-title h1 {
  font-size: 1.5rem;
  font-weight: var(--font-bold);
  color: var(--dark-theme-text-desaturated-blue);
  margin-left: 1rem;
}

.social__dashboard-followers {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1.3rem;
}
.social__dashboard-followers h1 {
  font-size: 5.5rem;
  font-weight: var(--font-bold);
  color: var(--dark-theme-text-white);
}
.social__dashboard-followers p {
  font-size: 1.5rem;
  font-weight: var(--font-thin);
  color: var(--dark-theme-text-desaturated-blue);
  text-transform: uppercase;
  letter-spacing: 3px;
}

.social__dashboard-daily {
  display: flex;
  align-items: center;
}
.social__dashboard-daily div {
  margin-bottom: 0.8rem;
  margin-right: 0.5rem;
}
.social__dashboard-daily h1 {
  font-size: 1.5rem;
  font-weight: var(--font-bold);
  color: var(--dark-theme-text-white);
}

.social__dashboard.twitter {
  border-top-color: var(--twitter);
}
.social__dashboard.twitter .twitter--daily h1 {
  color: var(--lime-green);
}

.social__dashboard.instagram {
  position: relative;
  border: none;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  margin-top: 4px;
}
.social__dashboard.instagram .instagram--daily h1 {
  color: var(--lime-green);
}
.social__dashboard.instagram::before {
  content: "";
  position: absolute;
  inset: -4px 0 0 0;
  background-image: linear-gradient(to right, var(--instagram-linear-from), var(--instagram-linear-to));
  border-top-left-radius: 0.6rem;
  border-top-right-radius: 0.6rem;
  border-bottom-left-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
  z-index: -1;
}

.social__dashboard.youtube {
  border-top-color: var(--youtube);
}
.social__dashboard.youtube .youtube--daily h1 {
  color: var(--bright-red);
}

.overview .overview__title {
  font-size: 2.8rem;
  font-weight: var(--font-bold);
  color: var(--dark-theme-text-white);
  margin-bottom: 2rem;
}

.overview__card-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(8, 1fr);
  gap: 2rem;
  place-items: center;
}

.overview__card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  max-width: 50rem;
  height: 16rem;
  padding: 3rem 4rem;
  background: var(--dark-theme-card-background);
  border-radius: 0.7rem;
}
.overview__card .overview__card-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.overview__card .overview__card-top h1 {
  font-size: 1.8rem;
  font-weight: var(--font-bold);
  color: var(--dark-theme-text-desaturated-blue);
}
.overview__card .overview__card-top div {
  width: 2.8rem;
}
.overview__card .overview__card-top div img {
  width: 100%;
}
.overview__card .overview__card-bottom {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.overview__card .overview__card-bottom h1 {
  font-size: 4.3rem;
  font-weight: var(--font-bold);
  color: var(--dark-theme-text-white);
}
.overview__card .overview__card-bottom .overview__card-percent {
  display: flex;
  align-items: center;
  margin-bottom: 0.7rem;
}
.overview__card .overview__card-bottom .overview__card-percent .overview__bottom-icons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0.5rem;
}
.overview__card .overview__card-bottom .overview__card-percent h3 {
  color: var(--lime-green);
}

.card_2 .overview__card-bottom .overview__card-percent h3 {
  color: var(--bright-red);
}

.card_7 .overview__card-bottom .overview__card-percent h3 {
  color: var(--bright-red);
}

.card_8 .overview__card-bottom .overview__card-percent h3 {
  color: var(--bright-red);
}

.attribution {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 4rem 0 3rem;
}
.attribution p {
  font-size: 1.8rem;
  font-weight: var(--font-bold);
  color: var(--dark-theme-text-desaturated-blue);
  margin-top: 1rem;
}
.attribution a {
  font-size: 2.5rem;
  background: linear-gradient(to right, var(--dark-theme-toggle-btn-linear-from), var(--dark-theme-toggle-btn-linear-to));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.attribution a:nth-child(2) {
  background: linear-gradient(to right, var(--instagram-linear-from), var(--instagram-linear-to));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.attribution span {
  font-size: 3rem;
  background: linear-gradient(to right, var(--instagram-linear-from), var(--instagram-linear-to));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

body.lightMode {
  background: var(--light-theme-background);
}
body.lightMode .dashboard__header h1 {
  color: var(--light-theme-text-dark);
}
body.lightMode .dashboard__header p {
  color: var(--light-theme-text-gray-blue);
}
body.lightMode .dashboard__toggle .dashboard__toggle-title h3 {
  color: var(--light-theme-text-gray-blue);
}
body.lightMode .dashboard__toggle-btn {
  background: var(--light-theme-toggle-btn);
}
body.lightMode .dashboard__toggle-btn .toggle-indicator {
  left: 24px;
  background: var(--light-theme-background);
}
body.lightMode .social__dashboard {
  background-color: var(--light-theme-card-bg);
}
body.lightMode .social__dashboard .social__dashboard-title h1 {
  color: var(--light-theme-text-gray-blue);
}
body.lightMode .social__dashboard .social__dashboard-followers h1 {
  color: var(--light-theme-text-dark);
}
body.lightMode .social__dashboard .social__dashboard-followers p {
  color: var(--light-theme-text-gray-blue);
}
body.lightMode .social__dashboard.facebook .facebook--daily h1 {
  color: var(--lime-green);
}
body.lightMode .overview .overview__title {
  color: var(--light-theme-text-gray-blue);
}
body.lightMode .overview .overview__card {
  background: var(--light-theme-card-bg);
}
body.lightMode .overview .overview__card .overview__card-top h1 {
  color: var(--light-theme-text-gray-blue);
}
body.lightMode .overview .overview__card .overview__card-bottom h1 {
  color: var(--light-theme-text-dark);
}
body.lightMode .attribution p {
  color: var(--light-theme-text-dark);
}

@media only screen and (min-width: 40em) {
  .social {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
  .social .social__dashboard {
    max-width: 50rem;
  }
  .overview__card-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }
  .overview__card-container .overview__card {
    max-width: 50rem;
  }
}
@media only screen and (min-width: 68.75em) {
  html {
    font-size: 62.5%;
  }
  body {
    padding: 3rem 12rem;
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
  }
  .dashboard__header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    margin: 0;
  }
  .dashboard__header-text {
    padding: none;
    border: none;
  }
  .dashboard__toggle {
    padding: none;
  }
  .dashboard__toggle-btn {
    margin-left: 1.5rem;
  }
  .dashboard__toggle-btn .toggle-indicator {
    width: 1.7rem;
    height: 1.7rem;
    position: absolute;
    top: 4px;
    left: 4px;
  }
  .social {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    align-items: center;
    gap: 3rem;
    margin-bottom: 2rem;
  }
  .social .social__dashboard {
    height: 21rem;
    min-width: 21rem;
  }
  .social .social__dashboard.instagram {
    height: calc(21rem - 4px);
  }
  .overview__card-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
  .attribution {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
  }
  .attribution p {
    margin: 0;
    margin-left: 2rem;
  }
  .attribution p:nth-child(2) {
    margin-bottom: 5px;
  }
  body.lightMode .dashboard__header .toggle-indicator {
    left: 28px;
  }
}